<script>
import ProductCounter from '@/components/ProductCounter.vue';
import NumberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import API_BASE_URL from '@/config';

export default {
  components: { ProductCounter },
  data() {
    return {
      productAmount: 1,

      productData: null,

      dataLoading: false,
      dataLoadingFailed: false
    };
  },
  filters: {
    NumberFormat
  },
  computed: {
    product() {
      return this.productData ? this.productData : [];
    }
  },
  methods: {
    addToCart() {
      this.$store
        .dispatch('addProductToCart', {
          productId: +this.$route.params.id,
          amount: +this.productAmount
        })
        .then(() => this.$store.dispatch('updateProductAddedStatus'));
    },
    productAmountUpdate(counter) {
      this.productAmount = counter;
    },
    loadProductData() {
      this.dataLoading = true;
      this.dataLoadingFailed = false;

      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products/${+this.$route.params.id}`)
          .then((response) => (this.productData = response.data))
          .catch(() => (this.dataLoadingFailed = true))
          .then(() => (this.dataLoading = false));
      }, 400);
    }
  },
  created() {
    this.loadProductData();
  },
  watch: {
    '$route.params.id': function () {
      this.loadProductData();
    }
  }
};
</script>
