import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import API_BASE_URL from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],

    userAccessKey: null,

    cartProductsData: [],

    loadCart: null,

    productAdded: null,
    productAddSending: null,

    orderInfo: null
  },
  mutations: {
    nullCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo
    },
    cartAmountUpdate(state, { productId, amount }) {
      state.cartProducts.find((el) => el.productId === productId).amount = amount;
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId);
    },

    syncCartProduct(state) {
      state.cartProducts = state.cartProductsData.map((item) => {
        return {
          productId: item.product.id,
          amount: item.quantity
        }
      })
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, cartProducts) {
      state.cartProductsData = cartProducts;
    }
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const product = state.cartProductsData.find((p) => (p.product.id === item.productId)).product;

        return {
          ...item,
          product
        }
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    }
  },
  actions: {
    loadOrderInfo(context, orderId) {
      axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
        params: {
          userAccessKey: context.state.userAccessKey
        }
      }).then((response) => {
        context.commit('updateOrderInfo', response.data)
      })
    },
    loadCart(context) {
      context.state.loadCart = true;

      clearTimeout(context.state.loadcCartTimer);
      context.state.loadcCartTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
          .then((response) => {
            if (!context.state.userAccessKey) {
              localStorage.setItem('userAccessKey', response.data.user.accessKey);
              context.commit('updateUserAccessKey', response.data.user.accessKey);
            }

            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProduct');
          })
          .then(() => (context.state.loadCart = false))
      }, 400);
    },
    addProductToCart(context, { productId, amount }) {
      context.state.productAdded = false;
      context.state.productAddSending = true;

      context.state.loadCart = true;

      clearTimeout(context.state.loadcCartTimer);
      context.state.loadcCartTimer = setTimeout(() => {
        axios.post(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount
        }, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        }).then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProduct');
        })
          .then(() => (context.state.productAdded = true))
          .then(() => (context.state.productAddSending = false))
          .then(() => (context.state.loadCart = false))
      }, 300)
    },
    updateCartProductAmount(context, { productId, amount }) {
      context.commit('cartAmountUpdate', { productId, amount });

      if (amount < 1) {
        return
      }

      axios.put(`${API_BASE_URL}/api/baskets/products`, {
        productId,
        quantity: amount
      }, {
        params: {
          userAccessKey: context.state.userAccessKey
        }
      }).then((response) => {
        context.commit('updateCartProductsData', response.data.items);
      }).catch(() => (context.commit('syncCartProduct')))
    },
    deleteCartProduct(context, productId) {
      context.commit('deleteCartProduct', productId);

      axios.delete(`${API_BASE_URL}/api/baskets/products`, {
        params: {
          userAccessKey: context.state.userAccessKey
        },
        data: {
          productId
        }
      }).then((response) => {
        context.commit('updateCartProductsData', response.data.items)
      });
    },
    updateProductAddedStatus(context) {
      setTimeout(() => {
        context.state.productAdded = false
      }, 3000)
    }
  },
});
