<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        @click.prevent="paginate(page - 1)"
        href="#"
        aria-label="Предыдущая страница"
      >
        <!-- :class="{'pagination__link--disabled': page === 1}" -->
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in paginationPagesQuantity" :key="pageNumber">
      <a
        class="pagination__link"
        href="#"
        :class="{ 'pagination__link--current': pageNumber === page }"
        @click.prevent="paginate(pageNumber)"
      >
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        @click.prevent="paginate(page + 1)"
        href="#"
        aria-label="Следующая страница"
      >
        <!-- :class="{'pagination__link--disabled': page === 1}" -->
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'showQuantity'],
  computed: {
    // высчитываем сколько элементов пагинации нужно отображать
    paginationPagesQuantity() {
      return Math.ceil(this.count / this.showQuantity);
    },
  },
  methods: {
    paginate(pageNumber) {
      // зацикливание
      this.$emit(
        'paginate',
        pageNumber < 1
          ? this.paginationPagesQuantity
          : pageNumber > this.paginationPagesQuantity
          ? 1
          : pageNumber
      );

      // блокировка
      // if (pageNumber >= 1 && pageNumber <= this.paginationPagesQuantity) this.$emit('paginate', pageNumber);
    },
  },
};
</script>
