<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span v-if="getProductsLength" class="content__info"
        >Всего товаров: {{ getProductsLength }}
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :categiry-id.sync="filterCategiryId"
        :colorId.sync="filterColorId"
        :page.sync="page"
      />

      <section class="catalog">
        <div
          v-if="dataLoading"
          style="
            display: flex;
            justify-content: center;
            align-items: flex-start;
          "
        >
          <p style="color: #333; font-size: 25px">Загрузка товаров...</p>
          <img
            width="65"
            height="65"
            src="img/preloader.gif"
            alt="Типа анимация загрузки"
          />
        </div>

        <div
          v-if="dataLoadingFailed"
          style="display: flex; justify-content: center"
        >
          <p style="color: #333; font-size: 25px; text-align: center">
            Ой... Что-то пошло не так!<br />Попробуйте обновить страницу.
          </p>
        </div>

        <div
          v-if="!dataLoading && !dataLoadingFailed && !getProductsLength"
          style="
            display: flex;
            justify-content: center;
            align-items: flex-start;
          "
        >
          <p style="color: #333; font-size: 25px">Ничего не найдено!</p>
        </div>

        <ProductList v-if="!dataLoading" :products="products" />

        <AppPagination
          v-if="!dataLoadingFailed"
          v-model="page"
          :count="getProductsLength"
          :show-quantity="productsShowQuantity"
        />
      </section>
    </div>
  </main>
</template>

<script>
import ProductFilter from '@/components/ProductFilter.vue';
import ProductList from '@/components/ProductList.vue';
import AppPagination from '@/components/AppPagination.vue';
import axios from 'axios';
import API_BASE_URL from '@/config';

export default {
  components: {
    ProductList,
    AppPagination,
    ProductFilter
  },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategiryId: 0,
      filterColorId: 0,

      page: 1,
      productsShowQuantity: 3,

      productsData: null,

      dataLoading: false,
      dataLoadingFailed: false
    };
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items : [];
    },
    getProductsLength() {
      return this.productsData ? this.productsData.pagination.total : 0;
    }
  },
  methods: {
    loadDroductsData() {
      this.dataLoading = true;
      this.dataLoadingFailed = false;

      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products?page`, {
            params: {
              page: this.page,
              limit: this.productsShowQuantity,
              categoryId: this.filterCategiryId,
              colorId: this.filterColorId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo
            }
          })
          .then((response) => (this.productsData = response.data))
          .catch(() => (this.dataLoadingFailed = true))
          .then(() => (this.dataLoading = false));
      }, 200);
    }
  },
  watch: {
    page() {
      this.loadDroductsData();
    },
    filterPriceFrom() {
      this.loadDroductsData();
    },
    filterPriceTo() {
      this.loadDroductsData();
    },
    filterCategiryId() {
      this.loadDroductsData();
    },
    filterColorId() {
      this.loadDroductsData();
    }
  },
  created() {
    this.loadDroductsData();
  }
};
</script>
