<template>
  <li class="cart__item product">
    <div class="product__pic">
      <router-link :to="`product/${item.product.id}`">
        <img
          :src="item.product.image.file.url"
          width="120"
          height="120"
          alt="Название товара"
        />
      </router-link>
    </div>
    <router-link :to="`product/${item.product.id}`">
      <h3 class="product__title" style="color: #222">
        {{ item.product.title }}
      </h3>
    </router-link>
    <!-- <p class="product__info">Объем: <span>128GB</span></p> -->
    <span class="product__code"> Артикул: {{ item.product.id }} </span>

    <ProductCounter
      :counterValue="productAmount"
      @amountUpdate="productAmountUpdate"
    />

    <b class="product__price">
      {{ (item.amount * item.product.price) | NumberFormat }}
    </b>

    <button
      class="product__del button-del"
      style="cursor: pointer"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct(item.product.id)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import ProductCounter from '@/components/ProductCounter.vue';
import NumberFormat from '@/helpers/numberFormat';

export default {
  props: ['item'],
  components: { ProductCounter },
  data() {
    return {
      productAmount: this.item.amount
    };
  },
  methods: {
    productAmountUpdate(counter) {
      this.productAmount = counter;

      this.$store.dispatch('updateCartProductAmount', {
        productId: this.item.productId,
        amount: this.productAmount
      });
    },
    deleteProduct(productId) {
      this.$store.dispatch('deleteCartProduct', productId);
    }
  },
  filters: {
    NumberFormat
  }
};
</script>

<style></style>
