<template>
  <div class="form__counter">
    <button
      type="button"
      aria-label="Убрать один товар"
      style="cursor: pointer"
      @click="counterUpdate('minus')"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="counter" @input="counterUpdate()" />

    <button
      type="button"
      aria-label="Добавить один товар"
      style="cursor: pointer"
      @click="counterUpdate('plus')"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ['counterValue'],
  data() {
    return {
      counter: this.counterValue
    };
  },
  methods: {
    counterUpdate(prop) {
      if (prop === 'minus') {
        if (this.counter > 1) this.counter -= 1;
      }

      if (prop === 'plus') {
        this.counter += 1;
      }

      this.$emit('amountUpdate', this.counter);
    }
  }
};
</script>
