<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="'/cart'">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>

      <span class="content__info"> Товары: {{ products.length }} </span>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="order"
      >
        <div class="cart__field">
          <div class="cart__data">
            <AppFormText
              :title="'ФИО'"
              :error="formErrors.name"
              :placeholder="'Введите ваше полное имя'"
              v-model="formData.name"
            />

            <AppFormText
              :title="'Адрес'"
              :error="formErrors.address"
              :placeholder="'Введите ваш адрес'"
              v-model="formData.address"
            />

            <AppFormText
              :title="'Телефон'"
              :error="formErrors.phone"
              :placeholder="'Введите ваш телефон'"
              v-model="formData.phone"
            />

            <AppFormText
              :title="'Email'"
              :error="formErrors.email"
              :placeholder="'Введите ваш email'"
              v-model="formData.email"
            />

            <AppFormTextarea
              :title="'Комментарий к заказу'"
              :error="formErrors.comment"
              :placeholder="'Ваши пожелания'"
              v-model="formData.comment"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked=""
                  />
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="500"
                  />
                  <span class="options__value"> Курьером <b>500 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="card"
                  />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="cash"
                  />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <OrderList
          :products="products"
          :totalPrice="$store.getters.cartTotalPrice"
          ><button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </OrderList>

        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import OrderList from '@/components/OrderList.vue';
import AppFormText from '@/components/AppFormText.vue';
import AppFormTextarea from '@/components/AppFormTextarea.vue';
import API_BASE_URL from '@/config';
import axios from 'axios';

export default {
  components: { OrderList, AppFormText, AppFormTextarea },
  data() {
    return {
      dataLoading: false,

      formData: {},
      formErrors: {},
      formErrorMessage: ''
    };
  },
  computed: {
    products() {
      return this.$store.getters.cartDetailProducts;
    }
  },
  methods: {
    order() {
      this.formErrors = {};
      this.formErrorMessage = '';

      axios
        .post(
          `${API_BASE_URL}/api/orders`,
          {
            ...this.formData
          },
          {
            params: {
              userAccessKey: this.$store.state.userAccessKey
            }
          }
        )
        .then((response) => {
          this.$store.commit('nullCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({
            name: 'orderInfo',
            params: { id: response.data.id }
          });
        })
        .catch((error) => {
          this.formErrors = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        });
    }
  }
};
</script>

<style></style>
